<template>
    <div class="vibration">
        <div class="bg">
            <div class="title">
                <span></span>
                <div class="title_name">换流变压器超声波局放实时监测</div>
            </div>
            <div class="topl">
                <div v-show="datalist.length == 0" class="nodata">无数据</div>
                <div class="table" v-show="datalist.length != 0">
                    <table class="list-table">
                        <thead>
                            <tr class="table-title">
                                <th class="tac"><span class="table_span"></span></th>
                                <th class="tac" v-for="(item0, i0) in datalist" :key="i0">
                                    <span class="titletop">{{ item0.device_name }}</span>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr class="table-content" v-for="(item, i) in  dataNameValue" :key="i">
                                <td class="table-type">{{ item.title }}</td>
                                <td v-for="(item1, i1) in datalist" :key="i1">{{ item1[item.value] }}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
        <div class="bg">
            <div class="title">
                <span></span>
                <div class="title_name">超声波局放趋势图</div>
            </div>
            <div class="choice">
                <div class="data">
                    <span>检测时间：</span>
                    <el-date-picker v-model="timedata" type="datetimerange" align="right" start-placeholder="开始日期"
                        value-format="yyyy-MM-dd HH:mm:ss" end-placeholder="结束日期"
                        :default-time="['00:00:00', '23:59:59']" :picker-options="pickerOptions">
                    </el-date-picker>
                </div>
                <div>
                    <span>设备属性：</span>
                    <el-select v-model="sdeviceattr" clearable placeholder="请选择">
                        <el-option v-for="item in deviceattr" :key="item.value" :label="item.attr_name"
                            :value="item.attr_id">
                        </el-option>
                    </el-select>
                </div>
                <div>
                    <span>选择设备:</span>
                    <el-select v-model="sdevicename" clearable placeholder="请选择">
                        <el-option v-for="item in devicename" :key="item.value" :label="item.device_name"
                            :value="item.device_id">
                        </el-option>
                    </el-select>
                </div>
                <div>
                    <el-button type="success" plain size="medium" class="button" @click="topsure">确认</el-button>
                </div>
            </div>
            <div>
                <vibrationEcharts :edata="edata" :sdevicename="sdevicename" v-if="edata" />
            </div>
        </div>
    </div>
</template>

<script>
import vibration from "./vibration";
export default vibration;
</script>
<style scoped>
@import url("vibration.css");
@import url("../../assets/css/common.css");
</style>


