// import tableList from './table.vue';
import vibrationEcharts from '../PartialDischarge/vibrationEcharts.vue';
import {
  partialdischarge_check,
  partialdischarge_chart,
  devicetype,
  devicename,
  deviceattr,
} from '../../utils/service';
export default {
  created() {

  },
  components: {
    vibrationEcharts

  },
  data() {
    return {
      devicename: '', // 设备名称
      devicetype: '', // 设备类型
      deviceattr: '', // 设备属性
      timedata: '',
      sdevicename: '', // 确认设备名称
      sdevicetype: '', // 确认设备类型
      sdeviceattr: '', // 确认设备属性
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      value2: '',
      value: '',
      dataNameValue: [{
          title: '超声波峰值(dB)',
          value: 'ultrasonicpeak'
        }, {
          title: '电波电压(V)',
          value: 'batteryvoltage'
        }, {
          title: '剩余电量(%)',
          value: 'batteryrp'
        },

      ],
      datalist: '',
      edata: '',
    };
  },

  mounted() {
    this.sTblist()
    this.getdevicename()
    this.getdeviceattr()
  },
  methods: {
    sTblist() {
      partialdischarge_check({}, res => {
        this.datalist = res.data
      });

    },
    topsure() {
      var startdate = ''
      var enddate = ''
      for (let index = 0; index < this.timedata.length; index++) {
        startdate = this.timedata[0];
        enddate = this.timedata[1];
      }
      var formData = {
        startdate: startdate,
        enddate: enddate,
        attrname: this.sdeviceattr,
        deviceid: this.sdevicename,
      }
      partialdischarge_chart(formData, res => {
        var datalist = res.data
        var listpush = []
        var listpush2 = []
        datalist.map(item => {
          listpush.push([item.acquisitiontime, item[this.sdeviceattr]])
          // listpush2.push([item.acquisitiontime, item.humidity])
        })
        this.edata = listpush
      });
    },
    // 获取设备类型信息
    // getdevicetype() {
    //   devicetype({}, res => {
    //     this.devicetype = res.data
    //   });
    // },
    // 根据设备类型获取设备名称
    getdevicename() {
      var formData = {
        devicetype: '021081',
      }
      devicename(formData, res => {
        this.devicename = res.data
      });
    },
    // 获取设备属性
    getdeviceattr() {
      var formData = {
        devicetype: '021081',
      }
      deviceattr(formData, res => {
        this.deviceattr = res.data
      });
    },
  }
};



